<template>
  <div class="news-feed">
    <div
      v-for="item in portfolioItems"
      :key="item.id"
      @click="navigateTo(item.id)"
      class="news-item">
      <img :src="`${baseUrl}/${item.folder}/${item.cover}`" class="news-image" />
      <div class="title-holder">
        <span class="title">{{ item.title }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  computed: {
    ...mapState([
      'portfolioItems',
      'baseUrl',
    ]),
  },
  methods: {
    navigateTo(itemId) {
      this.$router.push({ name: 'portfolio-item', params: { viewedId: itemId } });
    },
  },
};
</script>

