<template>
  <div>
    <div>
      <span class="title">{{ item.title }}</span>
    </div>
    <router-link :to="{ name: 'portfolio' }" class="small">Back</router-link>
    <Carousel :items="item.photos" :folder="item.folder" />
  </div>
</template>

<script>
import { mapState } from 'vuex';

import Carousel from './Carousel.vue';

export default {
  props: {
    viewedId: Number,
  },
  components: {
    Carousel,
  },
  data() {
    return {
      item: {},
    };
  },
  computed: {
    ...mapState([
      'portfolioItems',
      'baseUrl',
    ]),
  },
  mounted() {
    this.item = this.portfolioItems.find(e => e.id === this.viewedId);
  },
};
</script>

