<template>
  <div class="main" v-if="items">
    <div class="selected">
      <img :src="`${baseUrl}/${folder}/${items[selectedIndex]}`" />
    </div>
    <div class="thumbnails">
      <span @click="previousImage" class="navButton">&lt;</span>
      <div v-if="!isMobileDevice" class="thumbnail-holder">
        <img
          v-for="item in thumbnails"
          :src="`${baseUrl}/${folder}/${item.value}`"
          :key="item.index"
          @click="selectedIndex = item.index"
          :class="{ selected: item.index === selectedIndex }"
        />
      </div>
      <div v-else class="thumbnail-holder">
        <div
          class="image-dot" v-for="item in thumbnails"
          :key="item.index"
          @click="selectedIndex = item.index"
          :class="{ selected: item.index === selectedIndex }">
        </div>
      </div>
      <span @click="nextImage" class="navButton">&gt;</span>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  props: {
    items: Array,
    folder: String,
  },
  data() {
    return {
      selectedIndex: 0,
      maxThumbnails: 5,
    };
  },
  computed: {
    ...mapState([
      'baseUrl',
    ]),
    thumbnails() {
      let prependArray = [];
      const overflow = this.selectedIndex + this.maxThumbnails - this.itemsArray.length;
      const startingIndex = this.selectedIndex - overflow;

      if (overflow > 0) {
        prependArray = this.itemsArray.slice(
          startingIndex < 0 ? 0 : startingIndex,
          this.selectedIndex,
        );
      }

      return [
        ...prependArray,
        ...this.itemsArray.slice(this.selectedIndex, this.selectedIndex + this.maxThumbnails),
      ];
    },
    itemsArray() {
      return this.items.map((x, index) => ({ index, value: x }));
    },
    isMobileDevice() {
      return (typeof window.orientation !== 'undefined')
        || (navigator.userAgent.indexOf('IEMobile') !== -1);
    },
  },
  methods: {
    nextImage() {
      this.selectedIndex = this.selectedIndex + 1;

      if (this.selectedIndex === this.items.length) {
        this.selectedIndex = 0;
      }
    },
    previousImage() {
      this.selectedIndex = this.selectedIndex - 1;

      if (this.selectedIndex === -1) {
        this.selectedIndex = this.items.length - 1;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.main {
  margin-bottom: 30px;
  max-width: 900px;
  padding: 30px;
  margin: 0 auto;

  .selected {
    align-items: center;
    box-sizing: border-box;
    color: #fff;
    display: flex;
    height: 450px;
    justify-content: center;
    margin-bottom: 30px;

    img {
      max-height: 100%;
      max-width: 100%;
    }
  }

  .thumbnails {
    height: 110px;
    padding: 0 5px;
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    justify-content: center;

    @media screen and (max-width: $mediaMaxWidth) {
      height: 20px;
    }

    .thumbnail-holder {
      display: flex;
      flex-direction: row;
      justify-content: center;

      img {
        max-height: 90%;
        max-width: 100%;
        cursor: pointer;
        margin: auto 0;
        margin-left: 15px;

        &:first-child {
          margin-left: 0;
        }

        &:last-of-type {
          margin-right: 15px;
        }

        &.selected {
          max-height: 100%;
          border: 2px solid black;
        }
      }

      .image-dot {
        height: 20px;
        width: 20px;
        border: 1px solid $secondaryColour;
        border-radius: 50%;
        margin-left: 15px;

        &:last-of-type {
          margin-right: 15px;
        }

        &.selected {
          background-color: $primaryColour;
          border-color: $primaryColour;
        }
      }
    }
  }

  .navButton {
    padding: 5px;
    text-align: center;
    margin: auto 0;
    font-size: 1.8em;
    color: $secondaryColour;
    font-weight: 600;
    cursor: pointer;

    &:hover {
      color: $primaryColour;
      user-select: none;
    }

    @media screen and (max-width: $mediaMaxWidth) {
      padding: 0px 5px;
    }
  }
}
</style>
